<template>
  <div>
    <div v-if="!loading">
      <div
        v-if="!unknown && !loading && !retake"
        class="py-5 mx-auto mt-5 rounded  bg-gray-50 md:w-2/3 dark:bg-gray-800 dark:text-white"
      >
        <div class="py-3 text-center">
          <h2 class="my-4 text-2xl font-bold ma-auto" v-if="result[1]">
            You Scored: {{ result[1] }}
          </h2>

          <div class="py-5">
            <router-link
              :to="`/solution/${$route.params.id}?e=${$route.query.e}&w=${$route.query.w}&d=m`"
              class="my-2 primary_btn"
              v-if="this.ended < Date.now()"
            >
              View Solution
            </router-link>
            <button
              class="primary_btn"
              v-else
              @click="endalert"
              v-text="'View Solution'"
            ></button>
          </div>
        </div>

        <div v-if="!retake && solution.length > 0" class="my-5 text-center">
          <router-link
            :to="`/ranking/${this.$route.params.id}`"
            class="mx-auto my-2 primary_btn"
            text="View Your Rank"
          ></router-link>
        </div>
        <div v-if="!retake && solution.length > 0" class="text-center">
          <button
            class="mx-auto primary_btn"
            @click="startRetake"
            v-text="'Test Yourself Again'"
          ></button>
        </div>
        <div
          class="my-4 font-semibold text-center"
          v-if="!retake && solution.length > 0"
        >
          Retake exam score will not be recorded
        </div>
      </div>
      <div v-if="retake" class="pb-10">
        <div class="py-4 text-center">
          <h1 class="text-2xl font-bold dark:text-white">
            Exam - {{ this.$route.params.id }}
          </h1>
        </div>
        <div class="mx-2 md:w-2/3 md:mx-auto">
          <div
            v-for="(q, i) in solution"
            :key="i"
            class="p-5 mx-auto my-3 text-lg border border-gray-300 rounded-md shadow-lg  bg-gray-50 dark:bg-black dark:text-white kalpurush"
          >
            <div v-html="q.question"></div>
            <div class="mt-3">
              <div
                v-html="q.a"
                v-if="q.a"
                @click="solution[i].selected = q.a"
                :class="`cursor-pointer transform hover:scale-105 transition duration-150 hover:font-semibold  my-3 p-3 rounded  ${
                  q.a == q.selected
                    ? 'border-2 border-green-500 bg-green-300 dark:bg-indigo-400 dark:border-indigo-500 '
                    : 'dark:bg-gray-600 bg-gray-200 dark:text-white'
                }`"
              ></div>
              <div
                v-html="q.b"
                v-if="q.b"
                @click="solution[i].selected = q.b"
                :class="`cursor-pointer transform hover:scale-105 transition duration-150 hover:font-semibold  my-3 p-3 rounded  ${
                  q.b == q.selected
                    ? 'border-2 border-green-500 bg-green-300 dark:bg-indigo-400 dark:border-indigo-500 '
                    : 'dark:bg-gray-600 bg-gray-200 dark:text-white'
                }`"
              ></div>
              <div
                v-html="q.c"
                v-if="q.c"
                @click="solution[i].selected = q.c"
                :class="`cursor-pointer transform hover:scale-105 transition duration-150 hover:font-semibold  my-3 p-3 rounded  ${
                  q.c == q.selected
                    ? 'border-2 border-green-500 bg-green-300 dark:bg-indigo-400 dark:border-indigo-500 '
                    : 'dark:bg-gray-600 bg-gray-200 dark:text-white'
                }`"
              ></div>
              <div
                v-html="q.d"
                v-if="q.d"
                @click="solution[i].selected = q.d"
                :class="`cursor-pointer transform hover:scale-105 transition duration-150 hover:font-semibold  my-3 p-3 rounded  ${
                  q.d == q.selected
                    ? 'border-2 border-green-500 bg-green-300 dark:bg-indigo-400 dark:border-indigo-500 '
                    : 'dark:bg-gray-600 bg-gray-200 dark:text-white'
                }`"
              ></div>
            </div>
          </div>
          <div class="mb-10 text-center">
            <button
              @click="retakeResult"
              class="px-4 py-3 mx-auto font-semibold text-white rounded shadow outline-none  hover:shadow-lg bg-gradient-to-t from-green-500 to-green-400"
            >
              Submit
            </button>
          </div>
        </div>
      </div>
      <div v-if="questions.length > 0" class="pb-10">
        <div class="py-4 text-center">
          <h1 class="text-2xl font-bold dark:text-white">
            Exam - {{ this.$route.params.id }}
          </h1>
        </div>
        <div class="mx-2 md:w-2/3 md:mx-auto">
          <div
            v-for="(q, i) in questions"
            :key="i"
            class="p-5 mx-auto my-3 text-lg border border-gray-300 rounded-md shadow-lg  bg-gray-50 dark:bg-black dark:text-white kalpurush"
          >
            <div class="mb-2 text-lg font-semibold" v-html="q.question"></div>
            <div class="mt-3">
              <div
                v-html="q.a"
                v-if="q.a"
                @click="questions[i].selected = q.a"
                :class="`cursor-pointer transform hover:scale-105 transition duration-150 hover:font-semibold text-black  my-3 p-3 rounded  ${
                  q.a == q.selected
                    ? 'border-2 border-green-500 bg-green-300 dark:bg-indigo-400 dark:border-indigo-500 '
                    : 'dark:bg-gray-600 bg-gray-200 dark:text-white'
                }`"
              ></div>
              <div
                v-html="q.b"
                v-if="q.b"
                @click="questions[i].selected = q.b"
                :class="`cursor-pointer transform hover:scale-105 transition duration-150 hover:font-semibold  my-3 p-3 rounded  ${
                  q.b == q.selected
                    ? 'border-2 border-green-500 bg-green-300 dark:bg-indigo-400 dark:border-indigo-500 '
                    : 'dark:bg-gray-600 bg-gray-200 dark:text-white'
                }`"
              ></div>
              <div
                v-html="q.c"
                v-if="q.c"
                @click="questions[i].selected = q.c"
                :class="`cursor-pointer transform hover:scale-105 transition duration-150 hover:font-semibold  my-3 p-3 rounded  ${
                  q.c == q.selected
                    ? 'border-2 border-green-500 bg-green-300 dark:bg-indigo-400 dark:border-indigo-500 '
                    : 'dark:bg-gray-600 bg-gray-200 dark:text-white'
                }`"
              ></div>
              <div
                v-html="q.d"
                v-if="q.d"
                @click="questions[i].selected = q.d"
                :class="`cursor-pointer transform hover:scale-105 transition duration-150 hover:font-semibold  my-3 p-3 rounded  ${
                  q.d == q.selected
                    ? 'border-2 border-green-500 bg-green-300 dark:bg-indigo-400 dark:border-indigo-500 '
                    : 'dark:bg-gray-600 bg-gray-200 dark:text-white'
                }`"
              ></div>
            </div>
          </div>
          <div class="mb-10 text-center">
            <button
              @click="submitAns"
              :disabled="eload"
              class="px-4 py-3 mx-auto font-semibold text-white rounded shadow outline-none  hover:shadow-lg bg-gradient-to-t from-green-500 to-green-400"
            >
              {{ eload ? "Loading..." : "Submit" }}
            </button>
          </div>
        </div>
      </div>

      <div class="white--text fixed__timer" v-if="show">
        <div class="text-white bg-red-500 border border-white">
          {{ minute }} : {{ second }}
        </div>
      </div>
    </div>

    <div v-else class="flex items-center justify-center w-full h-screen">
      <div
        class="flex items-center justify-center -mt-24 space-x-1 text-sm text-gray-700  dark:text-white"
      >
        <svg
          fill="none"
          class="w-10 h-10 animate-spin"
          viewBox="0 0 32 32"
          xmlns="http://www.w3.org/2000/svg"
        >
          <path
            clip-rule="evenodd"
            d="M15.165 8.53a.5.5 0 01-.404.58A7 7 0 1023 16a.5.5 0 011 0 8 8 0 11-9.416-7.874.5.5 0 01.58.404z"
            fill="currentColor"
            fill-rule="evenodd"
          />
        </svg>

        <div class="text-lg">Loading ...</div>
      </div>
    </div>
  </div>
</template>
<script>
import { exams, auth } from "@/plugins/auth.js";
export default {
  data() {
    return {
      timing: null,
      solutionv: false,
      retake: false,
      questions: [],
      solution: [],
      result: [],
      unknown: false,
      loading: true,
      eload: false,
      minute: 0,
      second: 0,
      end: Date.now() + 12000000,
      show: false,
      url: "",
      solutionpdf: "",
      starttime: Date.now(),
      ended: null,
      inttime: 109090,
    };
  },
  beforeRouteEnter(to, from, next) {
    let { e, w } = to.query;
    if (e && w) {
      let ttrx = JSON.parse(localStorage.getItem("s22ssrb")).ttrx;
      if (ttrx) {
        next();
        return;
      }
    }
    next({
      path: "/",
    });
  },

  methods: {
    endalert() {
      this.$swal({
        icon: "warning",
        title: "Will be available after the time ends",
      });
    },
    getQuestionLink() {
      this.loading = true;

      this.url = exams + atob(this.$route.query.w);

      let roll = JSON.parse(localStorage.getItem("s22ssrb")).roll;

      fetch(this.url + "&type=check&phone=" + roll)
        .then((res) => res.json())
        .then((data) => {
          if (data.message == "exists") {
            this.result = data.result ? data.result : "";
            fetch(this.url + "&type=question")
              .then((r) => r.json())
              .then((d) => {
                this.solution = d;
                let ql = this.solution.length;
                this.inttime = ql * 60000;
                this.end = ql * 60000;
              });

            this.loading = false;
          } else {
            this.unknown = true;
            fetch(this.url + "&type=question")
              .then((r) => r.json())
              .then((d) => {
                this.questions = d.map((m) => {
                  return { ...m, selected: "" };
                });
                let ql = this.questions.length;
                this.inttime = ql * 60000;
                this.end = ql * 60000;
                this.loading = false;
                this.showRemaining(this.end + Date.now());
                this.autoSubmit();
              });
          }
        });
    },
    startRetake() {
      this.retake = true;
      this.showRemaining(Date.now() + this.end);
    },
    retakeResult() {
      let score = this.solution.reduce((a, c) => {
        if (c.correct == c.selected) {
          a += 1;
        }
        return a;
      }, 0);
      this.$swal({
        icon: "success",
        title: "Success",
        text: `You Scored ${score}`,
      }).then(() => {
        this.$router.replace(`/ranking/${this.$route.params.id}`);
      });
    },
    submitAns() {
      this.eload = true;
      let score = this.questions.reduce((a, c) => {
        if (c.correct == c.selected) {
          a += 1;
        }
        return a;
      }, 0);

      let { ttrx, roll, name, college, district, division, upazila } =
        JSON.parse(localStorage.getItem("s22ssrb"));
      fetch(this.url, {
        method: "POST",
        mode: "no-cors",
        cache: "no-cache",
        redirect: "follow",
        body: JSON.stringify({
          ttrx,
          roll,
          name,
          college,
          score,
          duration: Date.now() - this.starttime,
          district,
          division,
          upazila,
        }),
      }).then(() => {
        clearTimeout(this.timing);
        fetch(auth, {
          method: "POST",
          mode: "no-cors",
          body: JSON.stringify({
            routineUpdate: true,
            routineInfo: {
              row: this.user.row,
              rid: parseInt(this.$route.params.id),
              flag: true,
            },
          }),
        }).then(() => {
          let routine = JSON.parse(localStorage.getItem("routines22"));
          let idx = routine.findIndex((m) => m.id == this.$route.params.id);
          routine[idx].complete = true;
          this.$store.commit("SET_ROUTINE", routine);
          localStorage.setItem("routines22", JSON.stringify(routine));
          this.eload = false;
          this.$swal({
            icon: "success",
            title: "Success",
            text: `You Scored ${score}`,
          }).then(() => {
            this.$router.replace(`/ranking/${this.$route.params.id}`);
          });
        });
      });
    },
    autoSubmit() {
      this.timing = setTimeout(() => {
        this.show = false;

        this.submitAns();
      }, this.inttime);
    },
    showRemaining(end) {
      this.show = true;
      const timer = setInterval(() => {
        const distance = end - Date.now();
        if (distance < 0) {
          clearInterval(timer);
          return;
        }
        const days = Math.floor(distance / this._days);
        const hours = Math.floor((distance % this._days) / this._hours);
        const minutes = Math.floor((distance % this._hours) / this._minutes);
        const seconds = Math.floor((distance % this._minutes) / this._seconds);
        this.second = seconds < 10 ? "0" + seconds : seconds;
        this.minute = minutes < 10 ? "0" + minutes : minutes;
        this.hours = hours < 10 ? "0" + hours : hours;
        this.days = days < 10 ? "0" + days : days;
      }, 1000);
    },
  },

  created() {
    this.getQuestionLink();
  },
  computed: {
    _seconds: () => 1000,
    _minutes() {
      return this._seconds * 60;
    },
    _hours() {
      return this._minutes * 60;
    },
    _days() {
      return this._hours * 24;
    },
    user() {
      return this.$store.state.user;
    },
  },
};
</script>

<style lang="scss" scoped>
.fixed__timer {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100vw;
  color: white;
  font-size: 22px;
  position: fixed;
  bottom: 0;
}
.fixed__timer > div {
  font-size: 20px;

  padding: 10px;

  border-radius: 5px;
}
.solution {
  border: 1px solid rgba(black, 0.2);
  border-radius: 4px !important;
  background: linear-gradient(45deg, #f7f4f4, #d8d6d6b4) !important;
  box-shadow: 2px 2px 2px #c2c0c0, -2px -2px 4px #ffffff !important;
  div {
    color: black;
  }
  p {
    color: black;
  }
}
.examcontainer {
  background: rgba(255, 255, 255, 0.09) !important;
  backdrop-filter: blur(5px);
}
.examcontainerq {
  background: rgba(255, 255, 255, 0.774) !important;
  backdrop-filter: blur(5px);
}
</style>